.feed {
    flex: 0.6;
    margin: 0 20px;
    margin-top: 25px;
}

.feed_post {
    background-color: white;
    padding: 10px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.feed_input {
    border: 1px solid lightgray;
    border-radius: 30px;
    display: flex;
    padding: 10px;
    color: gray;
    padding-left: 15px;
}

.feed_input>.MuiSvgIcon-root {
    color: darkcyan;
    opacity: 0.7;
}

.feed_input>form {
    display: flex;
    width: 100%;
}

.feed_input>form>input {
    border: none;
    flex: 1;
    margin-left: 10px;
    outline-width: 0;
    font-weight: 600;
    color: darkcyan;
}

.feed_input>form>button {
    display: none;
}

.feed_inputoptions {
    display: flex;
    justify-content: space-evenly;
}